import React, { useCallback } from 'react';
import EntityForm from 'modules/entity/forms';
import { useAppState } from 'shared/state';
import { KntButton, WhiteModal } from 'shared/components/Elements';
import { useNotistack } from '../../../../../shared/hooks';
import AsyncSelectJs from '../../../../../shared/components/Fields/AsyncSelectJs';
import { KntTwilioStyle } from '../../../../../shared/services';
import { isMobile } from 'react-device-detect';
import { Field } from 'formik';

export default function AddMemberForm({ handleModalOpen, open, id }: any) {
	const { companyId } = useAppState();
	const closeModal = useCallback(() => handleModalOpen(null), []);
	const { showNotification } = useNotistack();
	return (
		<div>
			<WhiteModal
				handleModalOpen={handleModalOpen}
				className="h-full"
				open={Boolean(open)}
				maxWidth={500}
				minWidth={500}
				noPadding
			>
				{companyId ? (
					<EntityForm.Main
						entity="usersDepartment"
						name={`usersDepartment-${companyId}`}
						url={`${companyId}/departments/${id}/users/`}
						method="post"
						primaryKey="id"
						sendAsFormData={false}
						prependData
						normalizeData={(data: any) => data}
						fields={[
							{
								name: 'department',
								required: true,
								value: { id: id },
								type: 'object',
								onSubmitKey: ''
							},
							{
								name: 'user',
								required: true,
								onSubmitValue: (value) => ({ id: value.id }),
								value: null,
								type: 'object',
								onSubmitKey: ''
							}
						]}
						onSuccess={(response, resetForm): any => {
							showNotification({ message: 'Success! ' });
							resetForm();
							closeModal();
						}}
						onError={(error, resetForm): any => {
							showNotification({ message: 'Already exist', variant: 'error' });
							resetForm();
						}}
					>
						{({ isSubmitting, values, setFieldValue, errors, setFieldError, resetForm }) => {
							return (
								<div className="react-select-dropdown px-4">
									<h3 className="branch-form__heading font-base mb-4 font-medium">Add member</h3>
									<label className="ml-2 mb-2 block text-xs font-light text-kgrey-welcome">
										Member
									</label>
									<Field
										className="w-full"
										component={AsyncSelectJs}
										customStylesProps={KntTwilioStyle}
										name="user"
										placeholder="Choose member here"
										optionLabel={(option: any) => `${option?.first_name} ${option?.last_name}`}
										optionValue="id"
										isSearchable={!isMobile}
										isClearable
										isCached
										openMenuOnFocus
										loadOptionsKey="results"
										loadOptionsUrl={`/${companyId}/employee/`}
										loadOptionsParams={(search: string) => ({
											extra: {
												search,
												not_in_department: id,
												is_registered: 'true'
											}
										})}
									/>
									<div className="branch-form__footer flex justify-end px-4 pt-4 pb-9">
										<KntButton
											className={'mr-5 border border-kgrey text-kgrey'}
											color="default"
											onClick={closeModal}
										>
											Cancel
										</KntButton>
										<KntButton
											className={
												'dark:text-white dark:hover:bg-primary-300 dark:focus:ring-primary-300'
											}
											type="submit"
											isSubmitting={isSubmitting}
											color="primary"
										>
											Add
										</KntButton>
									</div>
								</div>
							);
						}}
					</EntityForm.Main>
				) : null}
			</WhiteModal>
		</div>
	);
}

AddMemberForm.defaultProps = {
	onSuccess: () => {}
};
