import React, { useCallback, useState } from 'react';
import EntityContainer from 'modules/entity/containers';
import {
	Pagination,
	SimpleBtn,
	TableBody,
	TableContainer,
	TableHead
} from 'shared/components/Elements';
import {
	ErrorBoundary,
	Loader,
	StyledTable,
	StyledTableCell,
	StyledTableHeadCell,
	StyledTableRow
} from 'shared/components';
import { BsFillTelephoneFill } from 'react-icons/bs';
import { MenuItem, Select } from '@material-ui/core';
import IconButton from '@material-ui/core/IconButton';
import { AiFillDelete, AiFillEye } from 'react-icons/ai';
import { MdModeEditOutline } from 'react-icons/md';
import { useAppState } from 'shared/state';
import { usePagination } from 'shared/hooks';
import { Link } from 'react-router-dom';
import { SubDepartmentDeleteModal } from './SubDepartmentDeleteModal';
import CreateSubDepartmentForm from './CreateSubDepartmentForm';
import EditSubDepartmentForm from './EditSubDepartmentForm';
import { useDebounce } from 'use-debounce';
import { CgSearch } from 'react-icons/cg';
import { useDepartmentList } from '../../../hooks/useDepartmentList';
import { getBusinessIcon } from 'shared/services/utils/getBusinessIcon';
import CopyEmailButton from '../../CopyEmailButton';

function SubDepartmentsTableRequest({ departmentId }: any) {
	const { companyId, isAdmin, isPermitted } = useAppState();
	const { page, handlePage } = usePagination('subPage');
	const [deleteModal, setDeleteModal] = useState<any | null>(null);
	const [formOpen, setFormOpen] = useState<boolean>(false);
	const [modifyModal, setModifyModal] = useState<any | null>(null);

	const handleDeleteModal = useCallback((item: any | null) => setDeleteModal(item), []);

	const handleModalOpen = () => setFormOpen((prev) => !prev);
	const handleModal = useCallback((item: any | null) => setModifyModal(item), []);

	const [query, setQuery] = useState('');
	const [searchQuery] = useDebounce(query, 500);

	const { handleDepOrderChange } = useDepartmentList('subDepartment', searchQuery, departmentId);

	const handleSearchQuery = (evt: React.ChangeEvent<HTMLInputElement>) => {
		setQuery(evt.target.value);
	};

	const renderSelectItems = (count: number | undefined) => {
		return [...Array(count)].map((_, key) => {
			return (
				<MenuItem key={key} value={key + 1}>
					{Number(key + 1)}
				</MenuItem>
			);
		});
	};

	return (
		<div>
			{companyId && departmentId ? (
				<div>
					<div className="prime-tabs flex items-center justify-between rounded-t-lg bg-kgrey-bg px-4 pt-3 pb-4 text-base font-medium dark:bg-primary-300 dark:text-white">
						<h4>Sub-departments</h4>
						<div className="flex items-center">
							<div className="group-sidebar__search relative flex-grow pr-2">
								<input
									type="search"
									value={query}
									className={
										'group-sidebar__search-input w-full rounded-10 border-b text-sm outline-none ' +
										'border-kgrey-border-solid' +
										' pl-9 transition focus:border-primary' +
										' placeholder-[rgba(45, 45, 45, 0.3)] py-2 dark:bg-kdark'
									}
									placeholder={'Search for sub departments'}
									onChange={handleSearchQuery}
								/>
								<div className="absolute top-1/2 left-2 -translate-y-1/2">
									<div className="text-kgrey">
										<CgSearch />
									</div>
								</div>
							</div>
							<SimpleBtn disabled={!isPermitted} onClick={handleModalOpen}>
								New Sub Department
							</SimpleBtn>
						</div>
					</div>
					<EntityContainer.All
						entity={'subDepartments'}
						name={`SubDepartments-${companyId}`}
						url={`/${companyId}/departments/${departmentId}/sub_departments/`}
						replaceIds
						params={{
							limit: 10,
							extra: {
								search: searchQuery
							},
							page
						}}
					>
						{({ items, isFetched, meta }) => {
							const requests = items.map((item: unknown) => item) as any[];

							return (
								<div>
									<TableContainer classes={{ root: 'relative ' }}>
										<StyledTable
											aria-label="simple table"
											classes={{ root: 'table-fixed dark-mode-styled-table' }}
										>
											<TableHead>
												<StyledTableRow>
													<StyledTableHeadCell style={{ width: '30%' }}>Name</StyledTableHeadCell>
													<StyledTableHeadCell style={{ width: '25%' }}>
														Phone Number
													</StyledTableHeadCell>
													<StyledTableHeadCell style={{ width: '25%' }}>Order</StyledTableHeadCell>
													<StyledTableHeadCell style={{ width: '10%' }}>
														<div className="flex-centered">Email</div>
													</StyledTableHeadCell>
													<StyledTableHeadCell style={{ textAlign: 'center', width: '15%' }}>
														Actions
													</StyledTableHeadCell>
												</StyledTableRow>
											</TableHead>
											<TableBody>
												{requests.map((item: any) => {
													const {
														id,
														icon,
														name,
														phone,
														sorder,
														acc_email_alias,
														acc_status,
														auto_case_create,
														is_staff
													} = item;
													const mobile = phone ? (phone[0] === '+' ? phone : `+${phone}`) : null;
													return (
														<StyledTableRow key={id}>
															{items.length ? (
																<>
																	{/*<StaffDepartmentIcon icon={icon} name={name} id={id} />*/}
																	<StyledTableCell>
																		<div className="flex items-center">
																			<div className="h-9 w-9 flex-shrink-0">
																				{getBusinessIcon(
																					icon?.thumbnail || icon?.thumbnail_150 || '',
																					name
																				)}
																			</div>
																			<Link to={`/departments/view/${id}/?is-staff=${is_staff}`}>
																				<p className="ml-2.5 text-sm font-medium text-primary transition hover:text-primary-dark">
																					{name}
																				</p>
																			</Link>
																		</div>
																	</StyledTableCell>
																	<StyledTableCell>
																		{mobile ? (
																			<div className="flex items-center">
																				<BsFillTelephoneFill color="#979797" size="15px" />
																				<a
																					href={`tel:${mobile}`}
																					className="test-kdark-text ml-2 align-middle text-sm text-primary hover:text-primary-dark"
																				>
																					{mobile}
																				</a>
																			</div>
																		) : null}
																	</StyledTableCell>
																	<StyledTableCell>
																		<div className="flex items-center px-2">
																			<Select
																				disabled={!isAdmin}
																				value={Number(item.sorder)}
																				onChange={(e) => handleDepOrderChange(e, item.id)}
																			>
																				{renderSelectItems(meta.count)}
																			</Select>
																		</div>
																	</StyledTableCell>
																	<StyledTableCell>
																		<div className=" flex-centered px-2">
																			{acc_email_alias && auto_case_create ? (
																				<CopyEmailButton copiedEmail={acc_email_alias} />
																			) : (
																				<>
																					{(() => {
																						if (!auto_case_create && acc_email_alias) {
																							return 'Deactivated';
																						} else {
																							return (
																								<>
																									{acc_status ? (
																										<p>Not ready yet</p>
																									) : (
																										<div>None</div>
																									)}
																								</>
																							);
																						}
																					})()}
																				</>
																			)}
																		</div>
																	</StyledTableCell>
																	<StyledTableCell>
																		<div className="flex items-center">
																			<IconButton
																				disabled={!isAdmin}
																				onClick={() => handleDeleteModal(item)}
																			>
																				<AiFillDelete color="#979797" size="18px" />
																			</IconButton>
																			<IconButton
																				disabled={!isAdmin}
																				aria-label="view"
																				color="primary"
																			>
																				<Link to={`/departments/view/${id}/?is-staff=${is_staff}`}>
																					<AiFillEye size="18px" />
																				</Link>
																			</IconButton>
																			<IconButton
																				disabled={!isPermitted}
																				onClick={() => handleModal(item)}
																			>
																				<MdModeEditOutline color="#979797" size="18px" />
																			</IconButton>
																		</div>
																	</StyledTableCell>
																</>
															) : (
																<StyledTableCell>
																	<div>No information</div>
																</StyledTableCell>
															)}
														</StyledTableRow>
													);
												})}
											</TableBody>
										</StyledTable>
										{!isFetched && <Loader tiled withOverlay />}
									</TableContainer>
									<div className="news__pagination my-4">
										{items.length ? (
											<Pagination
												count={meta?.last_page}
												page={page ? +page : 1}
												defaultPage={6}
												siblingCount={1}
												color={'primary'}
												onChange={handlePage}
											/>
										) : null}
									</div>
								</div>
							);
						}}
					</EntityContainer.All>
					<SubDepartmentDeleteModal handleModalOpen={handleDeleteModal} open={deleteModal} />
					<CreateSubDepartmentForm
						handleModalOpen={handleModalOpen}
						open={formOpen}
						id={departmentId}
						page={page}
						searchQuery={searchQuery}
					/>
					<EditSubDepartmentForm handleModalOpen={handleModal} open={modifyModal} />
				</div>
			) : (
				<div className="flex justify-center">
					<Loader />
				</div>
			)}
		</div>
	);
}

export default function SubDepartmentsTable(props: any) {
	return (
		<ErrorBoundary>
			<SubDepartmentsTableRequest {...props} />
		</ErrorBoundary>
	);
}
