import React, { useCallback, useState } from 'react';
import EntityContainer from 'modules/entity/containers';
import {
	Pagination,
	SimpleBtn,
	TableBody,
	TableContainer,
	TableHead
} from 'shared/components/Elements';
import {
	ErrorBoundary,
	Loader,
	StyledTable,
	StyledTableCell,
	StyledTableHeadCell,
	StyledTableRow
} from 'shared/components';
import IconButton from '@material-ui/core/IconButton';
import { AiFillDelete } from 'react-icons/ai';
import { useAppState } from 'shared/state';
import { usePagination } from 'shared/hooks';
import AddMemberForm from './AddMemberForm';
import { DepartmentMembersDelete } from './DepartmentMembersDelete';
import { CgSearch } from 'react-icons/cg';
import { useDebounce } from 'use-debounce';

import { getBusinessIcon } from 'shared/services/utils/getBusinessIcon';

function UsersDepartmentRequest({ departmentId }: any) {
	const { companyId, isAdmin, isPermitted } = useAppState();
	const { page, handlePage } = usePagination('memberPage');
	const [formOpen, setFormOpen] = useState<boolean>(false);
	const [deleteModal, setDeleteModal] = useState<any | null>(null);
	const [query, setQuery] = useState('');
	const [searchQuery] = useDebounce(query, 500);

	const handleDeleteModal = useCallback((item: any | null) => setDeleteModal(item), []);
	const handleModalOpen = () => setFormOpen((prev) => !prev);

	const handleSearchQuery = (evt: React.ChangeEvent<HTMLInputElement>) => {
		setQuery(evt.target.value);
	};

	return (
		<div>
			{companyId && departmentId ? (
				<div>
					<EntityContainer.All
						entity={'usersDepartment'}
						name={`usersDepartment-${companyId}`}
						url={`/${companyId}/departments/${departmentId}/users/`}
						params={{
							limit: 10,
							extra: {
								search: searchQuery
							},
							page
						}}
					>
						{({ items, isFetched, meta }) => {
							const requests = items.map((item: unknown) => item) as any[];
							return (
								<div>
									<div className="prime-tabs flex items-center justify-between rounded-t-lg bg-kgrey-bg px-4 pt-3 pb-4 text-base font-medium dark:bg-primary-300 dark:text-white">
										<h4>Members</h4>
										<div className="flex items-center">
											<div className="group-sidebar__search relative flex-grow pr-2">
												<input
													type="search"
													value={query}
													className={
														'group-sidebar__search-input w-full rounded-10 border-b text-sm outline-none ' +
														'border-kgrey-border-solid' +
														' pl-9 transition focus:border-primary' +
														' placeholder-[rgba(45, 45, 45, 0.3)] py-2 placeholder:text-base dark:bg-kdark'
													}
													placeholder={'Search for members'}
													onChange={handleSearchQuery}
												/>
												<div className="absolute top-1/2 left-2 -translate-y-1/2">
													<div className="text-kgrey">
														<CgSearch />
													</div>
												</div>
											</div>
											<SimpleBtn disabled={!isPermitted} onClick={handleModalOpen}>
												Add member
											</SimpleBtn>
										</div>
									</div>
									<TableContainer classes={{ root: 'relative ' }}>
										<StyledTable
											aria-label="simple table"
											classes={{ root: 'table-fixed dark-mode-styled-table' }}
										>
											<TableHead>
												<StyledTableRow>
													<StyledTableHeadCell style={{ width: '20%' }}>
														Full name
													</StyledTableHeadCell>
													<StyledTableHeadCell style={{ width: '15%' }}>Role</StyledTableHeadCell>
													<StyledTableHeadCell style={{ width: '15%' }}>Email</StyledTableHeadCell>
													<StyledTableHeadCell
														style={{
															textAlign: 'right',
															width: '20%',
															paddingRight: '40px'
														}}
													>
														Actions
													</StyledTableHeadCell>
												</StyledTableRow>
											</TableHead>
											<TableBody>
												{requests.map((item: any) => {
													const { id, user, role } = item;
													return (
														<StyledTableRow key={id}>
															{items.length ? (
																<>
																	<StyledTableCell>
																		<div className="flex items-center">
																			<div className="mr-2.5 h-9 w-9 flex-shrink-0">
																				{getBusinessIcon('', user.name)}
																			</div>
																			<p className="truncate text-sm font-medium">{user.name}</p>
																		</div>
																	</StyledTableCell>
																	<StyledTableCell>
																		<div className="text-sm">{role}</div>
																	</StyledTableCell>
																	<StyledTableCell>
																		<div className="truncate text-sm">{user.username}</div>
																	</StyledTableCell>
																	<StyledTableCell>
																		<div className="flex items-center justify-end">
																			<IconButton
																				disabled={!isAdmin}
																				onClick={() => handleDeleteModal(item)}
																			>
																				<AiFillDelete color="#979797" size="18px" />
																			</IconButton>
																		</div>
																	</StyledTableCell>
																</>
															) : (
																<StyledTableCell key={id}>
																	<div>No information</div>
																</StyledTableCell>
															)}
														</StyledTableRow>
													);
												})}
											</TableBody>
										</StyledTable>
										{!isFetched && <Loader tiled withOverlay />}
									</TableContainer>

									<div className="news__pagination my-4">
										{items.length ? (
											<Pagination
												count={meta?.last_page}
												page={page ? +page : 1}
												defaultPage={6}
												siblingCount={1}
												color={'primary'}
												onChange={handlePage}
											/>
										) : null}
									</div>
								</div>
							);
						}}
					</EntityContainer.All>
					<AddMemberForm handleModalOpen={handleModalOpen} open={formOpen} id={departmentId} />
					<DepartmentMembersDelete
						handleModalOpen={handleDeleteModal}
						open={deleteModal}
						departmentId={departmentId}
					/>
				</div>
			) : (
				<div className="flex justify-center">
					<Loader />
				</div>
			)}
		</div>
	);
}

export default function UsersDepartment(props: any) {
	return (
		<ErrorBoundary>
			<UsersDepartmentRequest {...props} />
		</ErrorBoundary>
	);
}
