import React, { useCallback, useState } from 'react';
import EntityForm from 'modules/entity/forms';
import { useAppState } from 'shared/state';
import DepartmentFormElements from './DepartmentFormElements';
import { WhiteModal } from 'shared/components/Elements';

import { useNotistack, useUpdateEntities } from 'shared/hooks';
import get from 'lodash/get';
import { useDepartmentContext } from '../context/DepartmentContext';

type Props = {
	handleModalOpen: (item: any | null) => void;
	open: any | null;
};

function getTimeUnit(unit: string) {
	switch (unit) {
		case 'MINUTES': {
			return { label: 'minutes', value: 'MINUTES' };
		}
		case 'HOURS': {
			return { label: 'hours', value: 'HOURS' };
		}
		case 'DAYS': {
			return { label: 'days', value: 'DAYS' };
		}
		default:
			return null;
	}
}

export default function EditDepartmentForm({ handleModalOpen, open }: Props) {
	const { companyId } = useAppState();
	const closeModal = useCallback(() => handleModalOpen(null), []);
	const { showNotification } = useNotistack();
	const { updateEntities } = useUpdateEntities();
	const { isStaff, setIsStaff } = useDepartmentContext();
	const [accStatusRequired, setAccStatusRequired] = useState<boolean>(false);

	return (
		<div>
			{companyId ? (
				<div>
					<WhiteModal
						maxWidth={466}
						minWidth={466}
						handleModalOpen={closeModal}
						open={Boolean(open)}
						scrollable
						noPadding
					>
						<EntityForm.Main
							entity="departments"
							name={`AllDepartments-${companyId}`}
							url={`${companyId}/departments/${open?.id}/`}
							method="patch"
							id={open?.id}
							updateData
							primaryKey="id"
							sendAsFormData={false}
							normalizeData={(data: any) => data}
							fields={[
								{
									name: 'icon',
									required: false,
									value: open?.icon,
									type: 'object',
									onSubmitKey: '',
									onSubmitValue: (value) => (value ? value : null)
								},
								{
									name: 'name',
									required: true,
									value: open?.name,
									onSubmitKey: ''
								},
								{
									name: 'link',
									required: false,
									value: open?.link,
									onSubmitKey: '',
									onSubmitValue: (value) => (value ? value : null)
								},
								{
									name: 'phone',
									required: false,
									value: open?.phone,
									onSubmitKey: ''
								},
								{
									name: 'case_escalation_time',
									required: false,
									value: open?.case_escalation_time ?? '',
									onSubmitValue: (value) => (value ? parseInt(value) : null),
									onSubmitKey: ''
								},
								{
									name: 'case_escalation_time_unit',
									required: false,
									value: getTimeUnit(open?.case_escalation_time_unit),
									onSubmitValue: (value) => value?.value,
									type: 'object',
									onSubmitKey: ''
								},
								{
									name: 'assign_required',
									required: false,
									value: open?.assign_required,
									onSubmitKey: ''
								},
								{
									name: 'initial_response_time',
									required: false,
									value: open?.initial_response_time ?? '',
									onSubmitValue: (value) => (value ? parseInt(value) : null),
									onSubmitKey: ''
								},
								{
									name: 'initial_response_time_unit',
									required: false,
									value: getTimeUnit(open?.initial_response_time_unit),
									onSubmitValue: (value) => value?.value,
									type: 'object',
									onSubmitKey: ''
								},
								{
									name: 'default_assignees',
									required: false,
									onSubmitKey: '',
									type: 'object',
									value: open?.default_assignees,
									onSubmitValue: (value) => (value.length ? value.map((item: any) => item.id) : [])
								},
								{
									name: 'data_field',
									required: false,
									onSubmitKey: '',
									type: 'object',
									value: open?.data_field
								},
								{
									name: 'auto_case_create',
									required: false,
									value: open?.auto_case_create,
									onSubmitKey: ''
								},
								{
									name: 'acc_status',
									onSubmitValue: (value) => get(value, 'id'),
									onSubmitKey: '',
									value: open?.acc_status,
									required: accStatusRequired,
									type: 'object'
								},
								{
									name: 'is_staff',
									value: isStaff === 'true',
									onSubmitKey: ''
								},
								{
									name: 'acc_linked_email',
									required: false,
									value: open?.acc_linked_email,
									onSubmitValue: (value) => (value ? value : null),
									onSubmitKey: ''
								},
								{
									name: 'acc_email_alias',
									required: false,
									value: open?.acc_email_alias,
									onSubmitKey: ''
								}
							]}
							onSuccess={(response, resetForm): any => {
								showNotification({ message: 'Success! ' });
								if (response?.default_assignees?.length < open?.default_assignees?.length) {
									updateEntities({
										entity: 'departments',
										entityId: open?.id,
										updatingData: {
											default_assignees: response?.default_assignees || []
										}
									});
								}
								resetForm();
								closeModal();
							}}
							onError={(error, resetForm): any => {
								showNotification({
									message: error?.response?.data?.message ?? 'Something went wrong',
									variant: 'error'
								});
								resetForm();
							}}
						>
							{({ isSubmitting, values, setFieldValue, errors, setFieldError, resetForm }) => {
								if (process.env.NODE_ENV === 'development') {
									console.log('values edit department', values);
								}

								return (
									<DepartmentFormElements
										values={values}
										setFieldValue={setFieldValue}
										setFieldError={setFieldError}
										isSubmitting={isSubmitting}
										errors={errors}
										closeModal={closeModal}
										isCreate={false}
										resetForm={resetForm}
										departmentId={open?.id}
										setAccStatusRequired={setAccStatusRequired}
									/>
								);
							}}
						</EntityForm.Main>
					</WhiteModal>
				</div>
			) : null}
		</div>
	);
}

EditDepartmentForm.defaultProps = {
	onSuccess: () => {}
};
