import React, { useCallback, useState } from 'react';
import { ErrorBoundary, Loader } from 'shared/components';
import { useAppState } from 'shared/state';
import EntityContainer from 'modules/entity/containers';
import { useParams } from 'react-router';
import { getBusinessIcon } from 'shared/services/utils/getBusinessIcon';
import { IconMembers } from 'shared/icons';
import CardDetailsInfo from 'shared/components/CardDetailsInfo';
import SubDepartmentsTable from './components/SubDepartmentsTable';
import UsersDepartment from './components/UsersDepartmentTable';
import EditDepartmentForm from '../EditDepartment';
import { DepartmentContextProvider } from '../../context/DepartmentContext';
import { IconDetailsId } from 'shared/icons/IconDetailsId';
import { IconDesign } from 'shared/icons/IconDesign';
import { IconPhone } from 'shared/icons/IconPhone';
import EditSubDepartmentForm from './components/EditSubDepartmentForm';
import { SimpleBtn } from 'shared/components/Elements';
import { DepartmentNavigationBreadCrumb } from '../DepartmentNavigationBreadCrumb';

type ParamsDepartmentDetails = {
	id: string;
};

function DepartmentDetailsInner() {
	const { companyId, isAdmin } = useAppState();
	const { id } = useParams<ParamsDepartmentDetails>();
	const [modifyModal, setModifyModal] = useState<any | null>(null);
	const handleModal = useCallback((item: any | null) => setModifyModal(item), []);

	return (
		<div className="relative flex min-h-full max-w-full flex-grow flex-col p-4">
			{companyId ? (
				<div className="mt-5">
					<EntityContainer.One
						primaryKey="id"
						id={id}
						entity="departments"
						name={`Department-${companyId}`}
						url={`${companyId}/departments/${id}/`}
						params={{}}
						relations={''}
					>
						{({ item, isFetched }) => {
							const detailsItem = item as any;
							const { icon, name, id, phone, members_count, hierarchy, is_staff } = detailsItem;
							const mobile = phone && phone[0] === '+' ? phone : `+${phone}`;
							const isSubDep = detailsItem?.department;

							return (
								<>
									{isFetched ? (
										<div>
											<DepartmentNavigationBreadCrumb
												departmentId={id}
												is_staff={is_staff}
												hierarchy={hierarchy}
											/>
											<div className=" absolute top-0 left-0 w-full bg-kred-pink-200">
												{!isAdmin ? (
													<p className="my-1 text-center text-xs text-kred-pink">
														You do not have permission to perform some actions, please check with
														admin
													</p>
												) : null}
											</div>
											<h2 className="mb-6 text-xl font-bold dark:text-white md:text-3xl">
												{isSubDep ? 'Sub ' : null} Department Details
											</h2>
											<div className="mb-2 flex flex-wrap items-center sm:mb-16">
												<div className="order-1 mr-4 h-28 w-28 flex-shrink-0 rounded-full bg-kgrey-bg p-7 dark:bg-kdark-lighter sm:mr-12">
													{getBusinessIcon(icon?.thumbnail || icon?.thumbnail_150 || '', name)}
												</div>
												<div className="order-last ml-auto flex flex-wrap py-4 sm:flex-nowrap lg:order-2 lg:ml-0 lg:py-0">
													<div className="mr-4 mb-4 sm:mr-7 sm:mb-0">
														<CardDetailsInfo
															fontSize={'text-lg'}
															color={'text-kred-pink'}
															bgColor={'bg-kgrey-bg'}
															title={'Department id:'}
															count={id}
														>
															<IconDetailsId />
														</CardDetailsInfo>
													</div>
													<div className="mr-4 mb-4 sm:mr-7 sm:mb-0">
														<CardDetailsInfo
															fontSize={'text-lg'}
															bgColor={'bg-kgrey-bg'}
															title={'Department name:'}
															count={name}
														>
															<IconDesign />
														</CardDetailsInfo>
													</div>
													<div className="mr-4 mb-4 sm:mr-7 sm:mb-0">
														<CardDetailsInfo
															fontSize={'text-lg'}
															bgColor={'bg-kgrey-bg'}
															title={'Members count:'}
															count={members_count}
														>
															<IconMembers />
														</CardDetailsInfo>
													</div>
													{phone ? (
														<div className="mr-4 mb-4 sm:mr-7 sm:mb-0">
															<a href={`tel:${mobile}`} className="">
																<CardDetailsInfo
																	fontSize={'text-lg'}
																	bgColor={'bg-kgrey-bg'}
																	title={'Phone:'}
																	count={phone}
																>
																	<IconPhone />
																</CardDetailsInfo>
															</a>
														</div>
													) : null}
												</div>
												<div className="order-3 ml-auto py-4">
													<SimpleBtn
														disabled={!isAdmin}
														type="button"
														onClick={() => handleModal(detailsItem)}
													>
														Edit {isSubDep ? 'sub ' : null} department
													</SimpleBtn>
												</div>
											</div>
										</div>
									) : (
										<div className="mb-4 flex justify-center">
											<Loader />
										</div>
									)}
									{isSubDep ? (
										<EditSubDepartmentForm handleModalOpen={handleModal} open={modifyModal} />
									) : (
										<EditDepartmentForm handleModalOpen={handleModal} open={modifyModal} />
									)}
								</>
							);
						}}
					</EntityContainer.One>
					<SubDepartmentsTable departmentId={id} />
					<UsersDepartment departmentId={id} />
				</div>
			) : (
				<div className="flex justify-center">
					<Loader />
				</div>
			)}
		</div>
	);
}

export function DepartmentDetails() {
	return (
		<ErrorBoundary>
			<DepartmentContextProvider>
				<DepartmentDetailsInner />
			</DepartmentContextProvider>
		</ErrorBoundary>
	);
}
