import React, { useCallback, useState } from 'react';
import EntityForm from 'modules/entity/forms';
import { useAppState } from 'shared/state';
import { WhiteModal } from 'shared/components/Elements';
import SubDepartmentFormElements from './SubDepartmentFormElements';
import { useLoadAll, useNotistack } from '../../../../../shared/hooks';
import get from 'lodash/get';

type CreateSubDepartmentFormType = {
	handleModalOpen: any;
	open: boolean;
	id: number;
	searchQuery: string;
	page: number;
};

export default function CreateSubDepartmentForm({
	handleModalOpen,
	open,
	id,
	searchQuery,
	page
}: CreateSubDepartmentFormType) {
	const { companyId } = useAppState();
	const closeModal = useCallback(() => handleModalOpen(null), []);
	const { showNotification } = useNotistack();
	const { reloadEntityAll } = useLoadAll();
	const [accStatusRequired, setAccStatusRequired] = useState<boolean>(false);
	return (
		<div>
			<WhiteModal
				minWidth={466}
				maxWidth={466}
				handleModalOpen={handleModalOpen}
				open={Boolean(open)}
				scrollable
				noPadding
			>
				{companyId ? (
					<EntityForm.Main
						entity="subDepartments"
						name={`SubDepartments-${companyId}`}
						url={`${companyId}/departments/`}
						method="post"
						primaryKey="id"
						sendAsFormData={false}
						normalizeData={(data: any) => data}
						appendData
						fields={[
							{
								name: 'icon',
								required: false,
								value: null,
								onSubmitKey: '',
								type: 'object',
								onSubmitValue: (value) => (value ? value : null)
							},
							{
								name: 'name',
								required: true,
								value: '',
								onSubmitKey: ''
							},
							{
								name: 'link',
								required: false,
								value: null,
								onSubmitKey: '',
								onSubmitValue: (value) => (value ? value : null)
							},
							{
								name: 'phone',
								required: false,
								value: null,
								onSubmitKey: ''
							},
							{
								name: 'auto_case_create',
								required: false,
								value: false,
								onSubmitKey: ''
							},
							{
								name: 'acc_status',
								onSubmitValue: (value) => get(value, 'id'),
								onSubmitKey: '',
								type: 'object',
								required: accStatusRequired
							},
							{
								name: 'acc_linked_email',
								required: false,
								value: null,
								onSubmitValue: (value) => (value ? value : null),
								onSubmitKey: ''
							}
						]}
						onSuccess={(response, resetForm): any => {
							showNotification({ message: 'Success! ' });
							resetForm();
							reloadEntityAll({
								entity: 'subDepartments',
								name: `SubDepartments-${companyId}`,
								url: `/${companyId}/departments/${id}/sub_departments/`,
								params: {
									page: page,
									limit: 10,
									extra: {
										search: searchQuery
									}
								}
							});
							closeModal();
						}}
						onError={(error, resetForm): any => {
							showNotification({ message: 'Already exist', variant: 'error' });
							resetForm();
						}}
					>
						{({ isSubmitting, values, setFieldValue, errors, setFieldError, resetForm }) => {
							return (
								<SubDepartmentFormElements
									values={values}
									setFieldValue={setFieldValue}
									setFieldError={setFieldError}
									isSubmitting={isSubmitting}
									errors={errors}
									closeModal={closeModal}
									resetForm={resetForm}
									id={id}
									setAccStatusRequired={setAccStatusRequired}
								/>
							);
						}}
					</EntityForm.Main>
				) : null}
			</WhiteModal>
		</div>
	);
}

CreateSubDepartmentForm.defaultProps = {
	onSuccess: () => {}
};
