import { Link } from 'react-router-dom';
import clsx from 'clsx';
import { BreadCrumbs } from 'shared/components/Elements';
import React from 'react';

type Props = {
	departmentId: number;
	is_staff: boolean;
	hierarchy: Array<{ name: string; id: number }>;
};

export function DepartmentNavigationBreadCrumb({ departmentId, is_staff, hierarchy }: Props) {
	return (
		<BreadCrumbs maxItems={4} aria-label="breadcrumb" separator=">" className="!pb-4">
			<Link className="hover:underline" to={`/departments/?is-staff=${is_staff}`}>
				{is_staff ? 'Staff Departments' : 'Client Departments'}
			</Link>
			{hierarchy?.map((link: Record<string, any>) => (
				<Link
					key={link.id}
					className={clsx('hover:underline', {
						'font-medium text-primary': link.id === departmentId
					})}
					to={`/departments/view/${link.id}/?is-staff=${is_staff}`}
				>
					{link.name}
				</Link>
			))}
		</BreadCrumbs>
	);
}
