import React, { useCallback, useState } from 'react';
import EntityForm from 'modules/entity/forms';
import EntityContainer from 'modules/entity/containers';
import { useAppState } from 'shared/state';
import { WhiteModal } from 'shared/components/Elements';
import SubDepartmentFormElements from './SubDepartmentFormElements';
import { useNotistack, useUpdateEntities } from '../../../../../shared/hooks';
import { StaffDepartmentRequestTypes } from '../../../../../shared/typings/department.types';
import get from 'lodash/get';

type Props = {
	handleModalOpen: (item: StaffDepartmentRequestTypes | null) => void;
	open: any | null;
};

function getTimeUnit(unit: string) {
	switch (unit) {
		case 'MINUTES': {
			return { label: 'minutes', value: 'MINUTES' };
		}
		case 'HOURS': {
			return { label: 'hours', value: 'HOURS' };
		}
		case 'DAYS': {
			return { label: 'days', value: 'DAYS' };
		}
		default:
			return null;
	}
}

export default function EditSubDepartmentForm({ handleModalOpen, open }: Props) {
	const { companyId } = useAppState();
	const closeModal = useCallback(() => handleModalOpen(null), []);
	const { showNotification } = useNotistack();

	const [accStatusRequired, setAccStatusRequired] = useState<boolean>(false);

	const { updateEntities } = useUpdateEntities();

	return (
		<div>
			{companyId && open?.id ? (
				<div>
					<EntityContainer.One
						id={open?.id}
						relations={''}
						entity={'subDepartments'}
						name={`SubDepartments-${companyId}`}
						url={`/${companyId}/departments/${open?.id}/`}
						params={{}}
					>
						{({ item, isFetched }) => {
							const subDepartment = item as any;
							return (
								<>
									{isFetched ? (
										<WhiteModal
											minWidth={466}
											maxWidth={466}
											handleModalOpen={closeModal}
											open={Boolean(open)}
											scrollable
											noPadding
										>
											<EntityForm.Main
												entity="subDepartments"
												name={`SubDepartments-${companyId}`}
												url={`${companyId}/departments/${subDepartment?.id}/`}
												method="patch"
												primaryKey="id"
												id={subDepartment?.id}
												sendAsFormData={false}
												updateData
												normalizeData={(data: any) => data}
												fields={[
													{
														name: 'icon',
														required: false,
														value: subDepartment?.icon,
														onSubmitKey: '',
														onSubmitValue: (value) => (value ? value : null),
														type: 'object'
													},
													{
														name: 'name',
														required: true,
														value: subDepartment?.name,
														onSubmitKey: ''
													},
													{
														name: 'link',
														required: false,
														value: subDepartment?.link,
														onSubmitKey: '',
														onSubmitValue: (value) => (value ? value : null)
													},
													{
														name: 'phone',
														required: false,
														value: subDepartment?.phone,
														onSubmitKey: ''
													},
													{
														name: 'is_staff',
														required: false,
														value: false,
														onSubmitKey: ''
													},
													{
														name: 'case_escalation_time',
														required: false,
														value: subDepartment?.case_escalation_time,
														onSubmitValue: (value) => (value ? parseInt(value) : null),
														onSubmitKey: ''
													},
													{
														name: 'case_escalation_time_unit',
														required: false,
														value: getTimeUnit(subDepartment?.case_escalation_time_unit),
														onSubmitValue: (value) => value?.value,
														type: 'object',
														onSubmitKey: ''
													},
													{
														name: 'initial_response_time',
														required: false,
														value: subDepartment?.initial_response_time,
														onSubmitValue: (value) => (value ? parseInt(value) : null),
														onSubmitKey: ''
													},
													{
														name: 'initial_response_time_unit',
														required: false,
														value: getTimeUnit(subDepartment?.initial_response_time_unit),
														onSubmitValue: (value) => value?.value,
														type: 'object',
														onSubmitKey: ''
													},
													{
														name: 'data_field',
														required: false,
														onSubmitKey: '',
														type: 'object',
														value: subDepartment?.data_field
													},
													{
														name: 'default_assignees',
														required: false,
														onSubmitKey: '',
														type: 'object',
														value: open?.default_assignees,
														onSubmitValue: (value) =>
															value.length ? value.map((item: any) => item.id) : []
													},
													{
														name: 'assign_required',
														required: false,
														value: open?.assign_required,
														onSubmitKey: ''
													},
													{
														name: 'auto_case_create',
														required: false,
														value: open?.auto_case_create,
														onSubmitKey: ''
													},
													{
														name: 'acc_status',
														onSubmitValue: (value) => get(value, 'id'),
														onSubmitKey: '',
														value: open?.acc_status,
														required: accStatusRequired,
														type: 'object'
													},
													{
														name: 'acc_linked_email',
														required: false,
														value: open?.acc_linked_email,
														onSubmitValue: (value) => (value ? value : null),
														onSubmitKey: ''
													},
													{
														name: 'acc_email_alias',
														required: false,
														value: open?.acc_email_alias,
														onSubmitKey: ''
													}
												]}
												onSuccess={(response, resetForm): any => {
													if (
														response?.default_assignees?.length < open?.default_assignees?.length
													) {
														updateEntities({
															entity: 'departments',
															entityId: open?.id,
															updatingData: {
																default_assignees: response?.default_assignees || []
															}
														});
													}
													resetForm();
													closeModal();
													showNotification({ message: 'Success! ' });
												}}
												onError={(error, resetForm): any => {
													showNotification({ message: 'Already exist', variant: 'error' });
													resetForm();
												}}
											>
												{({
													isSubmitting,
													values,
													setFieldValue,
													errors,
													setFieldError,
													resetForm
												}) => {
													return (
														<SubDepartmentFormElements
															values={values}
															setFieldValue={setFieldValue}
															setFieldError={setFieldError}
															isSubmitting={isSubmitting}
															errors={errors}
															closeModal={closeModal}
															isCreate={false}
															resetForm={resetForm}
															id={subDepartment?.id}
															departmentId={subDepartment?.id}
															setAccStatusRequired={setAccStatusRequired}
														/>
													);
												}}
											</EntityForm.Main>
										</WhiteModal>
									) : null}
								</>
							);
						}}
					</EntityContainer.One>
				</div>
			) : null}
		</div>
	);
}

EditSubDepartmentForm.defaultProps = {
	onSuccess: () => {}
};
